/**
 * @description 应用全局状态模块 - 处理应用级别的全局状态
 */

// 初始状态
const state = {
  appLoading: false,
  appInitialized: false,
  appError: null,
  sidebarCollapsed: localStorage.getItem('sidebarCollapsed') === 'true',
  darkMode: localStorage.getItem('darkMode') === 'true',
  currentRoute: null
};

// Getters
const getters = {
  appLoading: state => state.appLoading,
  appInitialized: state => state.appInitialized,
  appError: state => state.appError,
  sidebarCollapsed: state => state.sidebarCollapsed,
  darkMode: state => state.darkMode,
  currentRoute: state => state.currentRoute
};

// 引入常量类型
const SET_APP_LOADING = 'SET_APP_LOADING';
const SET_APP_INITIALIZED = 'SET_APP_INITIALIZED';
const SET_APP_ERROR = 'SET_APP_ERROR';
const TOGGLE_SIDEBAR = 'TOGGLE_SIDEBAR';
const TOGGLE_DARK_MODE = 'TOGGLE_DARK_MODE';
const SET_CURRENT_ROUTE = 'SET_CURRENT_ROUTE';

// Mutations
const mutations = {
  [SET_APP_LOADING](state, loading) {
    state.appLoading = loading;
  },
  [SET_APP_INITIALIZED](state, initialized) {
    state.appInitialized = initialized;
  },
  [SET_APP_ERROR](state, error) {
    state.appError = error;
  },
  [TOGGLE_SIDEBAR](state) {
    state.sidebarCollapsed = !state.sidebarCollapsed;
    localStorage.setItem('sidebarCollapsed', state.sidebarCollapsed);
  },
  [TOGGLE_DARK_MODE](state) {
    state.darkMode = !state.darkMode;
    localStorage.setItem('darkMode', state.darkMode);
    
    // 设置文档类以应用暗黑模式
    if (state.darkMode) {
      document.documentElement.classList.add('dark-mode');
    } else {
      document.documentElement.classList.remove('dark-mode');
    }
  },
  [SET_CURRENT_ROUTE](state, route) {
    state.currentRoute = route;
  }
};

// Actions
const actions = {
  /**
   * @description 设置应用加载状态
   * @param {Object} context - Vuex上下文
   * @param {boolean} loading - 加载状态
   */
  setAppLoading({ commit }, loading) {
    commit(SET_APP_LOADING, loading);
  },
  
  /**
   * @description 设置应用初始化状态
   * @param {Object} context - Vuex上下文
   * @param {boolean} initialized - 初始化状态
   */
  setAppInitialized({ commit }, initialized) {
    commit(SET_APP_INITIALIZED, initialized);
  },
  
  /**
   * @description 设置应用错误
   * @param {Object} context - Vuex上下文
   * @param {Error|null} error - 错误对象
   */
  setAppError({ commit }, error) {
    commit(SET_APP_ERROR, error);
  },
  
  /**
   * @description 切换侧边栏折叠状态
   * @param {Object} context - Vuex上下文
   */
  toggleSidebar({ commit }) {
    commit(TOGGLE_SIDEBAR);
  },
  
  /**
   * @description 切换暗黑模式
   * @param {Object} context - Vuex上下文
   */
  toggleDarkMode({ commit }) {
    commit(TOGGLE_DARK_MODE);
  },
  
  /**
   * @description 设置当前路由
   * @param {Object} context - Vuex上下文
   * @param {Object} route - 路由对象
   */
  setCurrentRoute({ commit }, route) {
    commit(SET_CURRENT_ROUTE, route);
  },
  
  /**
   * @description 初始化应用主题
   * @param {Object} context - Vuex上下文
   */
  initAppTheme({ state }) {
    // 初始化暗黑模式
    if (state.darkMode) {
      document.documentElement.classList.add('dark-mode');
    } else {
      document.documentElement.classList.remove('dark-mode');
    }
  }
};

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}; 